<template>
  <div class="container mt-5">
    <h1>Criar Novo Artigo ‚úçÔ∏è</h1>

    <div class="form-group mt-4">
      <label for="titulo">T√≠tulo</label>
      <input v-model="titulo" type="text" id="titulo" class="form-control" placeholder="Digite o t√≠tulo do artigo" />
    </div>
    <div class="form-group mt-3">
      <label for="categoria">Categoria</label>
      <input v-model="categoria" id="categoria" class="form-control" placeholder="Ex: ci√™ncia, tecnologia..." />
    </div>


    <div class="form-group mt-3">
      <label for="conteudo">Conte√∫do</label>
      <textarea v-model="conteudo" id="conteudo" class="form-control" rows="10"
        placeholder="Escreva o artigo aqui. Use ENTER para pular linhas e separar par√°grafos."></textarea>
    </div>

    <button class="btn btn-primary mt-3" @click="criarArtigo">Salvar Artigo</button>
    <router-link to="/" class="btn btn-secondary mt-3 ml-2">‚Üê Voltar</router-link>

    <div v-if="mensagem" class="alert alert-info mt-3">
      {{ mensagem }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      titulo: '',
      conteudo: '',
      mensagem: '',
      categoria: ''
    }
  },
  methods: {
    async criarArtigo() {
      if (!this.titulo || !this.conteudo) {
        this.mensagem = 'Preencha o t√≠tulo e o conte√∫do antes de salvar.'
        return
      }

      try {
         const resposta = await fetch(`/api/artigos`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            titulo: this.titulo,
            conteudo: this.conteudo, // string; back converte p/ array
            categoria: this.categoria || 'geral'
          })
        })

        const data = await resposta.json()
        this.mensagem = data.mensagem || 'Artigo criado com sucesso!'

        // limpa os campos
        this.titulo = ''
        this.conteudo = ''
      } catch (error) {
        console.error('Erro ao criar artigo:', error)
        this.mensagem = 'Erro ao criar o artigo. Verifique o servidor.'
      }
    }
  }
}
</script>
