<template>
  <div class="container mt-5">
    <h1>{{ sobre.titulo }}</h1>
    <div v-if="loading">Carregando...</div>
    <div v-else>
      <p v-for="(p, i) in sobre.paragrafos" :key="i" class="mt-3">
        {{ p }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SobreView',
  data() {
    return {
      sobre: { titulo: '', paragrafos: [] },
      loading: true
    }
  },
  async created() {
    try {
      const resposta = await fetch(`/api/artigos/info/sobre`)
      this.sobre = await resposta.json()
    } catch (error) {
      console.error('Erro ao carregar informa√ß√µes:', error)
    } finally {
      this.loading = false
    }
  }
}
</script>

<style scoped>
p {
  line-height: 1.6;
  font-size: 1.05rem;
}
</style>
