<template>
  <div class="container mt-5">
    <h1>Editar Artigo ‚úèÔ∏è</h1>

    <div v-if="loading">Carregando artigo...</div>

    <div v-else>
      <div class="form-group mt-4">
        <label for="titulo">T√≠tulo</label>
        <input v-model="titulo" type="text" id="titulo" class="form-control" />
      </div>
      <div class="form-group mt-3">
        <label for="categoria">Categoria</label>
        <input v-model="categoria" id="categoria" class="form-control" />
      </div>

      <div class="form-group mt-3">
        <label for="conteudo">Conte√∫do</label>
        <textarea v-model="conteudo" id="conteudo" class="form-control" rows="10"></textarea>
      </div>

      <button class="btn btn-primary mt-3" @click="salvarAlteracoes">üíæ Salvar</button>
      <router-link to="/" class="btn btn-secondary mt-3 ml-2">‚Üê Voltar</router-link>

      <div v-if="mensagem" class="alert alert-info mt-3">
        {{ mensagem }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['id'],
  data() {
    return {
      titulo: '',
      conteudo: '',
      mensagem: '',
      loading: true,
      categoria: ''
    }
  },
  async created() {
    try {
      const resposta = await fetch(`/api/artigos/${this.id}`);
      const artigo = await resposta.json()
      this.titulo = artigo.titulo
      this.conteudo = artigo.conteudo.join('\n\n') // üîπ junta par√°grafos em texto
    } catch (error) {
      console.error('Erro ao carregar artigo:', error)
    } finally {
      this.loading = false
    }
  },
  methods: {
    async salvarAlteracoes() {
      try {
        const resposta = await fetch(`/api/artigos/${this.id}`, {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            titulo: this.titulo,
            conteudo: this.conteudo,   // string; back converte p/ array
            categoria: this.categoria
          })
        })

        const data = await resposta.json()
        this.mensagem = data.mensagem || 'Artigo atualizado com sucesso!'
      } catch (error) {
        console.error('Erro ao atualizar artigo:', error)
        this.mensagem = 'Erro ao atualizar o artigo.'
      }
    }
  }
}
</script>
<style scoped>
textarea {
  white-space: pre-wrap;
  font-family: 'Courier New', monospace;
}

.alert {
  font-weight: 500;
}
</style>
