<template>
  <div class="container mt-5">
    <router-link to="/" class="btn btn-secondary mb-3">‚Üê Voltar</router-link>

    <div v-if="loading">Carregando artigo...</div>

    <div v-else-if="artigo">
      <h1>{{ artigo.titulo }}</h1>
      <p class="text-muted">
        üè∑Ô∏è {{ artigo.categoria || 'geral' }} ‚Ä¢
        üìÖ {{ formatDate(artigo.dataPublicacao) }} ‚Ä¢
        üëÅÔ∏è {{ artigo.views || 0 }} ‚Ä¢
        ‚ù§Ô∏è {{ artigo.likes || 0 }}
      </p>
      <div class="mb-3">
        <button class="btn btn-warning mr-2" @click="editarArtigo">‚úèÔ∏è Editar</button>
        <button class="btn btn-danger mr-2" @click="deletarArtigo">üóëÔ∏è Deletar</button>
        <button class="btn btn-success mr-2" @click="curtirArtigo">üëç Curtir</button>
        <button class="btn btn-info" @click="exportarPDF">üìÑ Exportar PDF</button>
      </div>

      <div v-for="(p, i) in artigo.conteudo" :key="i" class="paragrafo">
        <p>{{ p }}</p>
      </div>

      <!-- Coment√°rios -->
      <div class="mt-5">
        <h3>üí¨ Coment√°rios</h3>
        <div v-if="artigo.comentarios && artigo.comentarios.length">
          <div v-for="(c, i) in artigo.comentarios" :key="i" class="comentario border-bottom pb-2 mb-2">
            <p><strong>{{ c.autor }}</strong> ‚Äî <small>{{ c.data }}</small></p>
            <p>{{ c.texto }}</p>
          </div>
        </div>
        <div v-else class="text-muted">Ainda n√£o h√° coment√°rios.</div>

        <div class="mt-3">
          <input v-model="novoComentario" placeholder="Escreva um coment√°rio..." class="form-control mb-2" />
          <button class="btn btn-primary" @click="enviarComentario">Enviar</button>
        </div>
      </div>
    </div>

    <div v-else>
      <p>Artigo n√£o encontrado.</p>
    </div>
  </div>
</template>

<script>
import jsPDF from 'jspdf'

export default {
  props: ['id'],
  data() {
    return {
      artigo: null,
      loading: true,
      novoComentario: ''
    }
  },
  async created() {
    try {
const resposta =  await fetch(`/api/artigos/${this.id}`)
      this.artigo = await resposta.json()
    } catch (error) {
      console.error('Erro ao carregar artigo:', error)
    } finally {
      this.loading = false
    }
  },
  methods: {
    editarArtigo() {
      this.$router.push(`/editar/${this.id}`)
    },
    async deletarArtigo() {
      if (!confirm('Tem certeza que deseja deletar este artigo? üò¢')) return
      try {
      const resposta = await fetch(`/api/artigos/${this.id}`,{
          method: 'DELETE'
        })
        const data = await resposta.json()
        alert(data.mensagem || 'Artigo deletado com sucesso!')
        this.$router.push('/')
      } catch (error) {
        console.error('Erro ao deletar artigo:', error)
      }
    },
    async curtirArtigo() {
      try {
      const resposta = await fetch(`/api/artigos/${this.id}/like`,{
          method: 'PUT'
        })
        const data = await resposta.json()
        this.artigo.likes = data.totalLikes
      } catch (error) {
        console.error('Erro ao curtir artigo:', error)
      }
    },
    async enviarComentario() {
      if (!this.novoComentario.trim()) return
      try {
        const resposta = await fetch(`/api/artigos/${this.id}/comentarios`,{
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            autor: 'Visitante',
            texto: this.novoComentario
          })
        })
        console.log('opa foi');
        
        const data = await resposta.json()
        this.artigo.comentarios.push(data.comentario)
        this.novoComentario = ''
        console.log( this.artigo.comentarios);
        
      } catch (error) {
        console.error('Erro ao enviar coment√°rio:', error)
      }
    },
    exportarPDF() {
      const doc = new jsPDF()
      doc.setFontSize(18)
      doc.text(this.artigo.titulo, 10, 20)
      doc.setFontSize(12)
      let y = 30
      this.artigo.conteudo.forEach(p => {
        const linhas = doc.splitTextToSize(p, 180)
        doc.text(linhas, 10, y)
        y += linhas.length * 8
      })
      doc.save(`${this.artigo.titulo}.pdf`)
    },
    formatDate(iso) {
      if (!iso) return 'sem data'
      const d = new Date(iso)
      if (isNaN(d)) return iso
      return d.toLocaleDateString('pt-BR')
    }
  }
}
</script>

<style scoped>
.paragrafo {
  margin-bottom: 1rem;
  line-height: 1.6;
  font-size: 1.05rem;
}

.comentario {
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 6px;
  padding: 0.5rem;
}
</style>
