<template>
  <nav class="header-bar">
    <div class="header-content">
      <div class="left">
        <router-link class="brand" to="/">üì∞ <strong>The Article</strong></router-link>
        <router-link class="link" to="/">In√≠cio</router-link>
        <router-link class="link" to="/sobre">Sobre</router-link>
      </div>

      <button @click="alternarTema" class="btn-theme">
        {{ tema === 'dark' ? 'üåû Claro' : 'üåô Escuro' }}
      </button>
    </div>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      tema: localStorage.getItem('tema') || 'light'
    }
  },
  mounted() {
    document.documentElement.setAttribute('data-theme', this.tema)
  },
  methods: {
    alternarTema() {
      this.tema = this.tema === 'dark' ? 'light' : 'dark'
      document.documentElement.setAttribute('data-theme', this.tema)
      localStorage.setItem('tema', this.tema)
    }
  }
}
</script>

<style scoped>
/* Header laranja cobrindo tudo */
.header-bar {
  background-color: #ffa500; /* Laranja fixo */
  width: 100%;
  padding: 0.8rem 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  border-bottom: 3px solid #4b3f4e;
}

/* Centraliza o conte√∫do dentro do header */
.header-content {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* √Årea esquerda (logo + links) */
.left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* Logo */
.brand {
  color: #000;
  font-size: 1.3rem;
  text-decoration: none;
}

/* Links */
.link {
  color: #000;
  font-weight: 500;
  text-decoration: none;
  transition: 0.2s;
}

.link:hover {
  text-decoration: underline;
}

/* Bot√£o fixo */
.btn-theme {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 6px 14px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: 0.3s;
}

