import Vue from 'vue'
import VueRouter from 'vue-router'
import ListaArtigos from '../components/ListaArtigos.vue'
import ArticleView from '../views/ArticleView.vue'
import SobreView from '../views/SobreView.vue'
import CriarArtigoView from '../views/CriarArtigoView.vue'
import EditarArtigoView from '../views/EditarArtigoView.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'lista', component: ListaArtigos },
  { path: '/article/:id', name: 'article', component: ArticleView, props: true },
  { path: '/sobre', name: 'sobre', component: SobreView },
  { path: '/criar', name: 'criar', component: CriarArtigoView },
  { path: '/editar/:id', name: 'editar', component: EditarArtigoView, props: true } // üëà rota nova
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
