<template>
  <div class="container mt-4">
    <div class="d-flex justify-content-between align-items-center mb-3">
      <h1>The Article üì∞</h1>
      <router-link to="/criar" class="btn btn-success">+ Criar Artigo</router-link>
    </div>

    <!-- Filtros -->
    <div class="mb-3 d-flex" style="gap: .5rem;">
      <input v-model="busca" class="form-control" placeholder="Buscar por t√≠tulo..." />
      <select v-model="categoriaSelecionada" class="form-control" style="max-width: 220px;">
        <option value="">Todas as categorias</option>
        <option v-for="cat in categoriasUnicas" :key="cat" :value="cat">{{ cat }}</option>
      </select>
      <select v-model="ordenacao" class="form-control" style="max-width: 180px;">
        <option value="recentes">Mais recentes</option>
        <option value="antigos">Mais antigos</option>
        <option value="titulo">Por t√≠tulo (A‚ÄìZ)</option>
      </select>
    </div>

    <div v-if="loading" class="text-center">Carregando...</div>

    <div v-else>
      <article
        v-for="artigo in artigosFiltrados"
        :key="artigo.id"
        class="artigo"
        @click="abrirArtigo(artigo.id)"
      >
        <h2 class="mb-1">{{ artigo.titulo }}</h2>
        <small class="text-muted d-block mb-2">
          üè∑Ô∏è {{ artigo.categoria || 'geral' }} ‚Ä¢ üìÖ {{ formatDate(artigo.dataPublicacao) }}
        </small>
        <p>{{ primeiroParagrafo(artigo) }}</p>
        <small class="text-muted">üëÅÔ∏è {{ artigo.views || 0 }} ‚Ä¢ ‚ù§Ô∏è {{ artigo.likes || 0 }}</small>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artigos: [],
      loading: true,
      busca: '',
      categoriaSelecionada: '',
      ordenacao: 'recentes'
    }
  },
  async created() {
    try {
      const r = await fetch('/api/artigos')
      this.artigos = await r.json()
    } catch (e) {
      console.error('Erro ao buscar artigos:', e)
    } finally {
      this.loading = false
    }
  },
  computed: {
    categoriasUnicas() {
      const set = new Set(this.artigos.map(a => a.categoria || 'geral'))
      return Array.from(set).sort((a,b)=>a.localeCompare(b))
    },
    artigosFiltrados() {
      let lista = this.artigos.slice()

      // filtro por categoria
      if (this.categoriaSelecionada) {
        const cat = this.categoriaSelecionada.toLowerCase()
        lista = lista.filter(a => (a.categoria || '').toLowerCase() === cat)
      }
      // filtro por t√≠tulo
      if (this.busca) {
        const term = this.busca.toLowerCase()
        lista = lista.filter(a => a.titulo.toLowerCase().includes(term))
      }
      // ordena√ß√£o
      if (this.ordenacao === 'titulo') {
        lista.sort((a, b) => a.titulo.localeCompare(b.titulo))
      } else if (this.ordenacao === 'antigos') {
        lista.sort((a, b) => new Date(a.dataPublicacao||0) - new Date(b.dataPublicacao||0))
      } else { // recentes
        lista.sort((a, b) => new Date(b.dataPublicacao||0) - new Date(a.dataPublicacao||0))
      }
      return lista
    }
  },
  methods: {
    abrirArtigo(id) {
      const destino = `/article/${id}`
      if (this.$route.path !== destino) this.$router.push(destino)
    },
    primeiroParagrafo(artigo) {
      const p = (artigo.conteudo && artigo.conteudo[0]) || ''
      return p.length > 140 ? p.substring(0, 140) + '...' : p
    },
    formatDate(iso) {
      if (!iso) return 'sem data'
      const d = new Date(iso)
      if (isNaN(d)) return iso // se j√° veio formatada
      return d.toLocaleDateString('pt-BR')
    }
  }
}
</script>

<style scoped>
.artigo {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 1.2rem;
  margin: 1rem 0;
  color: var(--text-color);
  cursor: pointer;
  transition: background-color .3s ease, transform .2s ease;
}
.artigo:hover { transform: scale(1.01); }
</style>
